import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ApplicationCenterPage } from './application-center.page';

const routes: Routes = [
  {
    path: '',
    component: ApplicationCenterPage
  },    
  {
    path: 'universities',
    loadChildren: () => import('../universities/universities.module').then( m => m.UniversitiesPageModule)
  },
  {
    path: 'personal-details',
    loadChildren: () => import('./personal-details/personal-details.module').then( m => m.PersonalDetailsPageModule)
  },
  {
    path: 'academic-details/:appID',
    loadChildren: () => import('./academic-details/academic-details.module').then( m => m.AcademicDetailsPageModule)
  },
  {
    path: 'upload-documents/:appID',
    loadChildren: () => import('./upload-documents/upload-documents.module').then( m => m.UploadDocumentsPageModule)
  },
  {
    path: 'service-selection/:appID',
    loadChildren: () => import('./service-selection/service-selection.module').then( m => m.ServiceSelectionPageModule)
  },
  {
    path: 'payment-selection/:appID',
    loadChildren: () => import('./payment-selection/payment-selection.module').then( m => m.PaymentSelectionPageModule)
  },
  {
    path: 'thank-you',
    loadChildren: () => import('./thank-you/thank-you.module').then( m => m.ThankYouPageModule)
  },
  {
    path: 'track-application/:appID',
    loadChildren: () => import('./track-application/track-application.module').then( m => m.TrackApplicationPageModule)
  },
  {
    path: 'checkout',
    loadChildren: () => import('./checkout/checkout.module').then( m => m.CheckoutPageModule)
  },
  {
    path: 'service-optional/:appID',
    loadChildren: () => import('./service-optional/service-optional.module').then( m => m.ServiceOptionalPageModule)
  }

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ApplicationCenterPageRoutingModule {}
